<template>
  <div>
    <div style="margin-bottom: 16px">
      <a-button type="primary" :disabled="!hasSelected" :loading="loading" @click="start">删除选中</a-button>
      <span style="margin-left: 8px">
        <template v-if="hasSelected">{{ `选中 ${selectedRowKeys.length} 条数据` }}</template>
      </span>
    </div>
    <a-table
      :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
      :columns="columns"
      :data-source="data"
      :scroll="{y: 450, scrollToFirstRowOnChange: true}"
      :pagination="pagination"
      @change="onChange"
    />
  </div>
</template>
<script>
import { getProxyList, removeProxy } from "network/proxy";

export default {
  name: "Proxy",
  data() {
    return {
      data: [],
      sortInfo: {
        columnKey: "score",
        field: "score",
        order: "ascend"
      },
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      selectedRowKeys: [],
      loading: false,
      interval: ""
    };
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let sortInfo = this.sortInfo;
      return [
        {
          title: "序号",
          dataIndex: "no"
        },
        {
          title: "代理",
          dataIndex: "proxy"
        },
        {
          title: "分数",
          dataIndex: "score",
          key: "score",
          sorter: true,
          sortDirections: ["descend", "ascend"]
        }
      ];
    }
  },
  activated() {
    const get = () => {
      this.getProxy();
      return get;
    };
    this.interval = setInterval(get(), 3000);
  },
  deactivated() {
    clearInterval(this.interval);
  },
  methods: {
    //   请求数据
    getProxy() {
      const queryVo = {
        currentPage: this.pagination.current,
        pageSize: this.pagination.pageSize,
        reverse: !(
          this.sortInfo.order === undefined || this.sortInfo.order === "ascend"
        )
      };
      getProxyList(queryVo).then(res => {
        this.data = [];
        this.pagination.total = res.data.data.total;
        for (let i = 0; i < this.pagination.pageSize; i++) {
          this.data.push({
            key: res.data.data.proxyList[i].proxy,
            no:
              i + this.pagination.pageSize * (this.pagination.current - 1) + 1,
            proxy: res.data.data.proxyList[i].proxy,
            score: res.data.data.proxyList[i].score
          });
        }
      });
    },
    async start() {
      this.loading = true;
      await removeProxy(this.selectedRowKeys).then(res => {
        this.loading = false;
        this.selectedRowKeys = [];
        this.getProxy();
      });
      this.$bus.$emit("removeProxy");
    },
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    onChange(pagination, filters, sorter) {
      this.pagination.current = pagination.current;
      this.sortInfo.order = sorter.order;
      this.getProxy();
    }
  }
};
</script>
